import {
  Box,
  Flex,
  Spacer,
  Show,
  Hide,
  Center,
  Heading,
  Text,
  Button,
} from "@chakra-ui/react";

import React from "react";
// import { HashLink as Link } from "react-router-hash-link";
import { Link } from "react-scroll";
import SideNav from "./SideNav";

export default function Navbar() {
  return (
    <Flex
      w="full"
      alignItems="center"
      gap="2"
      p={5}
      pl="40px"
      pr="40px"
      justifyContent="space-between"
      bg="transparent"
    >
      <Box>
        <Link
          to="/"
          activeClass="active"
          spy={true}
          smooth={true}
          duration={1000}
          activeClassName="selected"
        >
          <Heading fontSize={{ sm: "40px" }}>Protfolio </Heading>
        </Link>
      </Box>
      <Spacer />
      <Show breakpoint="(min-width: 1000px)">
        <Flex gap="5">
          <Center>
            <Link
              to="/"
              activeClass="active"
              spy={true}
              smooth={true}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">Home </Text>
            </Link>
          </Center>
          <Center>
            <Link
              activeClass="active"
              to="about"
              spy={true}
              smooth={true}
              offset={-70}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">About </Text>
            </Link>
          </Center>
          <Center>
            <Link
              to="project"
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">Projects </Text>
            </Link>
          </Center>
          <Center>
            <Link
              to="contact"
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={1000}
              activeClassName="selected"
            >
              <Text fontSize="1.5xl">Contacts </Text>
            </Link>
          </Center>

          
            <Center>
              <Button bgColor="teal" colorScheme="green">
                <a href="https://drive.google.com/file/d/1zd1O4TuLSML7sJuPV5DKgMJwUrE-YyK3/view?usp=sharing">
                  Resume
                </a>
              </Button>
            </Center>
         
        </Flex>
      </Show>
      <Show breakpoint="(max-width: 999px)">
        <SideNav />
      </Show>
    </Flex>
  );
}
