import { FormControl, Input,Box,Heading,Text, Container, Flex, Center } from "@chakra-ui/react";
import { Textarea } from '@chakra-ui/react'
import React from "react";

import "./Contact.css"




export default function Contact(){






    return (
      <div className="contact">
        <div className="heading">
          <div className="lines">
            <Heading>Contact</Heading>
          </div>
        </div>
        <div className="Container-contact">
          <div>
            <Heading>Contact Me</Heading>
            <Box mb="15px">
              <Text>mohanty.ryzen@gmail.com </Text>
            </Box>
            <Box mb="15px">
              <Text>6371660063</Text>
            </Box>
          </div>

          <div className="form-control">
            <FormControl isRequired>
              <Input placeholder="Your name" mb="15px" />
              <Input placeholder="Email" mb="15px" />

              <Box mb="15px">
                <Input placeholder="Subject" />
              </Box>
              <Box>
                <Textarea
                  placeholder="Here is a sample placeholder"
                  size="md"
                  resize={"none"}
                />
              </Box>
              <Center w="30%" mt="15px">
                <Input bgColor="teal" type="submit" placeholder="Submit" />
              </Center>
            </FormControl>
          </div>
        </div>
      </div>
    );

}