import {
  Button,
  Drawer,
  DrawerBody,
  useDisclosure,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  Heading,
  Center,
  Text,
} from "@chakra-ui/react";
import React from "react";
import { HamburgerIcon } from "@chakra-ui/icons";
import { Link } from "react-scroll";



export default function SideNav() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  return (
    <>
      <Button ref={btnRef} colorScheme="teal" onClick={onOpen}>
        <HamburgerIcon w={8} h={8} />
      </Button>
      <Drawer
        isOpen={isOpen}
        placement="left"
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />

          <Center mt="40px">
            <DrawerBody>
              <Center zIndex="1666">
                <Link
                  to="/"
                  activeClass="active"
                  spy={true}
                  smooth={true}
                  duration={1000}
                  activeClassName="selected"
                >
                  <Heading> Home</Heading>
                </Link>
              </Center>
            </DrawerBody>
          </Center>

          <Center mt="40px">
            <DrawerBody>
              <Center>
                <Link
                  activeClass="active"
                  to="about"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={1000}
                  activeClassName="selected"
                >
                  <Text> About</Text>
                </Link>
              </Center>
            </DrawerBody>
          </Center>

          <Center mt="40px">
            <DrawerBody>
              <Center>
                <Link
                  to="project"
                  activeClass="active"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={1000}
                  activeClassName="selected"
                >
                  <Text> Projects</Text>
                </Link>
              </Center>
            </DrawerBody>
          </Center>

          <Center mt="40px">
            <DrawerBody>
              <Center>
                <Link
                  to="contact"
                  activeClass="active"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={1000}
                  activeClassName="selected"
                >
                  <Text> Contacts</Text>
                </Link>
              </Center>
            </DrawerBody>
          </Center>

          <Center mt="40px">
            <DrawerBody>
              <Center>
                <Link>
                  <Text> Resume</Text>
                </Link>
              </Center>
            </DrawerBody>
          </Center>
        </DrawerContent>
      </Drawer>
    </>
  );
}
