import {
  Box,
  Flex,
  Heading,
  Image,
  Spacer,
  Center,
  Show,
  Hide,
  Button,
 spacer
} from "@chakra-ui/react";
import React from "react";
import Typical from "react-typical";
import linkdin from "./Images/linkedin-bounce.gif";
import Github from "./Images/icons8-github.gif";
import Twitter from "./Images/twitter.jpg";
import { Link } from "react-scroll";
import muna2 from "./Images/muna2.png"

export default function MyProfile() {
  return (
    <div>
      <Show breakpoint="(min-width: 900px)">
        <Flex pl="5%" gap="20%" mt="40px">
          <Box>
            <Center>
              <Heading display="flex">
                Hi !
                <Center>
                  <Image
                    src="https://media.giphy.com/media/hvRJCLFzcasrR4ia7z/giphy.gif"
                    w="50"
                    h={"50px"}
                  ></Image>
                </Center>
              </Heading>
            </Center>
            <Box>
              <Center>
                <Heading fontSize="3xl">My name is Jyotiranjan Mohanty</Heading>
              </Center>
            </Box>
            <Spacer />
            <Box mt="50px">
              <Heading color="teal.300" fontSize="2xl">
                <Center color="white">I am a </Center>
                <Center>
                  <Typical
                    loop={Infinity}
                    steps={[
                      "Full Stack Devloper",
                      1000,
                      "MERN Devloper",
                      1000,
                      "Youtuber",
                      1000,
                    ]}
                  />
                </Center>
              </Heading>
            </Box>
            <Center mt="20px" ml="30px">
              <Center display="flex" gap="18px">
                <Image src={linkdin} w="7%" borderRadius="30%" />
                <Image src={Github} w="7%" borderRadius="60%" />
                <Image src={Twitter} w="7%" borderRadius="60%" />
              </Center>
            </Center>
            <Box mt="25px">
              <Center>
                <Flex gap="10%">
                  <Button bgColor="teal" colorScheme="green">
                    Contact
                  </Button>
                  <Button bgColor="teal" colorScheme="green">
                    <Link
                      to="project"
                      activeClass="active"
                      spy={true}
                      smooth={true}
                      offset={-70}
                      duration={1000}
                      activeClassName="selected"
                    >
                      Projects
                    </Link>{" "}
                  </Button>
                </Flex>
              </Center>
            </Box>
          </Box>
          <Box right="2%">
            <Image
              id="my-prfl"
              src={muna2}
              w="80%"
              borderRadius="60%"
              bgColor="teal"
            />
          </Box>
        </Flex>
      </Show>

      <Hide breakpoint="(min-width: 900px)">
        <Center mt="30px">
          <Box>
            <Center>
              <Heading display="flex" fontSize={{ sm: "35px" }}>
                Hi !
                <Image
                  src="https://media.giphy.com/media/hvRJCLFzcasrR4ia7z/giphy.gif"
                  w="20px"
                  h={"20px"}
                ></Image>
              </Heading>
            </Center>
            <Center>
              <Box>
                <Heading fontSize={{ sm: "35px" }}>
                  My name is Jyotiranjan Mohanty
                </Heading>
              </Box>
            </Center>
            <Spacer />
            <Box mt="50px">
              <Center>
                <Heading mr="5px" fontSize={{ sm: "30px" }}>
                  I am a{" "}
                </Heading>
                <Heading color="#F56565" fontSize={{ sm: "30px" }}>
                  <Typical
                    loop={Infinity}
                    steps={[
                      " Full Stack Devloper",
                      1000,
                      " MERN Devloper",
                      1000,
                      " Youtuber",
                      1000,
                    ]}
                  />
                </Heading>
              </Center>
            </Box>
          </Box>
        </Center>
        <Center mt="20px" ml="30px">
          <Center display="flex" gap="18px">
            <Image src={linkdin} w="7%" borderRadius="30%" />
            <Image src={Github} w="7%" borderRadius="60%" />
            <Image src={Twitter} w="7%" borderRadius="60%" />
          </Center>
        </Center>
        <Box mt="25px">
          <Center>
            <Flex gap="10%">
              <Button bgColor="teal" colorScheme="green">
                Contact
              </Button>
              <Button bgColor="teal" colorScheme="green">
                <Link
                  to="project"
                  activeClass="active"
                  spy={true}
                  smooth={true}
                  offset={-70}
                  duration={1000}
                  activeClassName="selected"
                >
                  Projects
                </Link>{" "}
              </Button>
            </Flex>
          </Center>
        </Box>
      </Hide>
    </div>
  );
}
